.selection-box {
    float: left;
}

.selection-box:hover {
    cursor: pointer;
}

.mnemonic-phrase-box {
    background-color: #CCCCCC;
    padding: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.bold-phrase {
    margin-top: 15px;
    margin-bottom: 15px;
    font-weight: bold;
}

.mnemonic-word-bubble {
    background-color: #26A69A;
    color: #FFFFFF;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: 600;
    outline: none;
    border: none;
    border-radius: 16px;
    margin-right: 7px;
    margin-bottom: 5px;
    width: fit-content;
}

.word-close {
    background-color: transparent;
    border: none;
    color: #FFFFFF;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: 600;
}

.word-close:focus {
    outline: none
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}

