.keys-outer-gray {
    padding-top: 20px;
    padding-left: 20px;
    width: 100vw;
    max-width: 100%;
    min-height: 100vh;
    background-color: rgba(216,216,216,0.25);
    overflow: auto;
}

.button-style {
    width: 100%;
    background-color: transparent;
    border: none;
    position: relative;
    top: 10%;
    margin-bottom: 20px;
    margin-top: 20px;
}

.button-style:focus {
    outline: none;
}

.button-style:hover {
    text-decoration-line: underline;
}

.add-key-card {
    width: 300px;
    height: 200px;
    font-family: Roboto, serif;
    font-size: 16px;
    margin-top: 25px;
    overflow: auto;
    margin-right: 20px;
    padding: 20px;
    border: #26A69A dashed 1px;
}

.plus-sign {
    width: 30px;
    height: 30px;
    float: left;
    vertical-align: middle;
    box-shadow: rgba(23, 59, 85, 0.16) 0 3px 6px;
    border-radius: 50%;
    margin-right: 15px;
}

.or-style {
    position: relative;
    top: 11%;
    left: 40%;
    padding: 20px;
    font-size: 18px;
    font-weight: 500;
}