.network-button {
    outline: none;
    border: 2px solid #CCCCCC;
    border-radius: 8px;
    background-color: #FFFFFF;
    font-family: "Work Sans", serif;
    font-size: 16px;
    font-weight: 500;
    color: #444444;
    height: 38px;
    width: 130px;
    text-align: left;
    vertical-align: middle;
    padding: 5px 10px 2px 12px;
}

.network-button-selected {
    outline: none;
    border-radius: 4px;
    border: none;
    background-color: #26A69A;
    font-family: "Work Sans", serif;
    font-size: 16px;
    font-weight: 500;
    color: #FFFFFF;
    height: 38px;
    width: 130px;
    text-align: left;
    vertical-align: middle;
    padding: 5px 10px 2px 12px;
}

.dropdown-item {
    border: none;
    background-color: #FFFFFF;
    outline: none;
    height: 38px;
    width: 130px;
    display: block;
    text-align: left;
    font-family: "Helvetica Neue", serif;
    font-size: 14px;
    color: #444444;
}

.dropdown-item:hover {
    background-color: #F2F2F2;
}

.disabled-dropdown-item {
    border: none;
    background-color: #FFFFFF;
    outline: none;
    height: 38px;
    width: 130px;
    display: block;
    text-align: left;
    font-family: "Helvetica Neue", serif;
    font-size: 14px;
    color: #444444;
}

.disabled-dropdown-item:hover {
    cursor: not-allowed;
}

.network-dropdown {
    margin: 0;
    border-radius: 4px;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2);
    position: absolute;
}

.dropdown-title {
    font-family: "Helvetica Neue", serif;
    font-size: 16px;
    color: #444444;
    text-align: center;
    border-style: none none solid none;
    border-width: 2px;
    margin: 0;
    padding-bottom: 10px;
    padding-top: 10px;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
}


