.send-tab {
    background-color: rgba(38,166,154,0.15);
    margin-right: 30px;
    margin-left: 44px;
    margin-bottom: 25px;
    overflow: auto;
}

.field {
    margin-top: 20px;
    font-family: Roboto, serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 19px;
    color: rgba(0,0,0,0.85);
    padding-left: 13px;
    padding-top: 15px;
    padding-bottom: 10px;
}

.automated-field {
    font-family: Roboto, serif;
    font-weight: normal;
    font-size: 16px;
    color: rgba(0,0,0,0.85);
    line-height: 19px;
    padding-left: 13px;
    padding-bottom: 10px;
}

.address-box {
    color: #26A69A;
    font-weight: bold;
    line-height: 19px;
    border-style: solid;
    border-color: #26A69A;
    height: 31px;
    width: 502px;
    padding-left: 15px;
    padding-top: 11px;
    display: inline-block;
    margin-left: 10px;
    background-color: transparent;
    border-width: 3px;
    padding-bottom: 10px;
}

.address-box::placeholder {
    color: #444444;
    font-family: Roboto, serif;
    font-size: 16px;
    font-weight: 300;
}

.address-box:focus{
    outline: none;
}

.address-box-warning {
    background-color: transparent;
    border-width: 3px;
    padding-bottom: 10px;
    font-family: Roboto, serif;
    color: #26A69A;
    font-size: 16px;
    font-weight: bold;
    line-height: 19px;
    border-style: solid;
    border-color: red;
    height: 31px;
    width: 502px;
    padding-left: 15px;
    padding-top: 11px;
    display: inline-block;
    margin-left: 10px;
}

.amount-box {
    background-color: transparent;
    color: #26A69A;
    font-family: Roboto, serif;
    font-size: 16px;
    font-weight: bold;
    line-height: 31px;
    border-style: solid;
    border-color: #26A69A;
    border-width: 3px;
    height: 31px;
    width: 157px;
    padding-left: 15px;
    display: inline-block;
    margin-left: 10px;
    margin-right: 10px;
}

.amount-box::placeholder {
    color: #444444;
    font-family: Roboto, serif;
    font-size: 16px;
    font-weight: 300;
}

.amount-box:focus{
    outline: none;
}

.amount-box-warning {
    background-color: transparent;
    color: #26A69A;
    font-family: Roboto, serif;
    font-size: 16px;
    font-weight: bold;
    line-height: 31px;
    border-style: solid;
    border-color: red;
    border-width: 3px;
    height: 31px;
    width: 157px;
    padding-left: 15px;
    display: inline-block;
    margin-left: 10px;
    margin-right: 10px;
}

.amount-box-warning:focus {
    outline: none;
}

.currency {
    margin-right: 10px;
}

.send-offline-switch {

}

.offline-switch {
    margin-left: 15px;
    vertical-align: middle;
    width: 45px;
}

.offline-switch:hover {
    cursor: pointer;
}

.number {
    float: right;
    padding-right: 80%;
}

.error-msg {
    color: red;
    display: block;
    font-size: 14px;
    font-weight: normal;
    padding-top: 10px;
}

.add-recipient {
    padding-left: 13px;
    padding-bottom: 20px;
    font-weight: 300;
}

.plus-symbol {
    width: 20px;
    height: 20px;
    vertical-align: text-bottom;
}