.copy-button {
    height: 40px;
    width: 50px;
    background-color: #26A69A;
    border: none;
    color: #FFFFFF;
    margin-left: 20px;
    display: inline-block;
}

.copy-button:hover {
    border: solid #FFFFFF;
}

.copy-button:focus {
    outline: none
}

.copy-tooltip {
    font-family: Roboto, serif;
    font-weight: bold;
}