.left-menu {
    min-height: 100vh;
    width: 256px;
    border-style: none solid none none;
    border-width: 1px;
    border-color: #E3DFDF;
    float: left;
    margin-left: -256px;
    box-sizing: border-box;
}

.collapsed-left-menu {
    width: 60px;
    min-height: 100vh;
    border-style: none solid none none;
    border-width: 1px;
    border-color: #E3DFDF;
    float: left;
    margin-left: -60px;
    box-sizing: border-box;
}

.topl-logo {
    height: 73px;
    width: 95px;
    padding-left: 79px;
    padding-top: 28px;
    pointer-events: none;
}

.topl-small-logo {
    width: 38px;
    padding-left: 13px;
    padding-top: 28px;
    pointer-events: none;
}

.left-menu-items {
    padding-top: 43px;
}

.collapsed-menu-items{
    padding-top: 78px;
}

.sub-menu-item {
    color: #444444;
    font-family: 'Quicksand', serif;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 0;
    line-height: 18px;
    float: left;
    padding: 20px 50px 10px 72px;
    border: none;
    background-color: #FFFFFF;
}

.sub-menu-item:hover {
    color: #26A69A;
    font-weight: bold;
    cursor: pointer;
}

.sub-menu-item:focus {
    outline: none;
}

.sub-menu-item-open {
    color: #26A69A;
    font-family: 'Quicksand', serif;
    font-weight: bold;
    font-size: 14px;
    letter-spacing: 0;
    line-height: 18px;
    float: left;
    padding: 20px 50px 10px 72px;
    border: none;
    background-color: #FFFFFF;
}

.sub-menu-item-open:focus {
    outline: none;
}

.menu-button-clicked {
    outline:none;
    background-color: rgba(38,166,154,0.3);
    box-shadow: inset 3px 0 0 0 #26A69A;
    height: 40px;
    border: none;
}

.menu-item {
    padding-left: 18px;
    float: left;
    font-family: 'Quicksand', serif;
    font-weight: bold;
    font-size: 16px;
    color: #444444;
    letter-spacing: 0;
    line-height: 23px;
    text-align: left;
}

.balances-list {

}

ul {
    padding: 0;
}

.sub-buttons {
    outline:none;
    background-color: #FFFFFF;
    height: 40px;
    border: none;
    padding-left: 15px;
    padding-top: 20px;
}

.sub-buttons:hover{
    cursor: pointer;
}

.menu-icon {
    height: 27px;
    width: 30px;
    padding-left: 23px;
    float: left;
}