button:hover {
  cursor: pointer;
}

.page {
  display: inline-block;
  min-height: 100vh;
}

.open-page {
  vertical-align: top;
  float: right;
  height: 100vh;
  max-width: 100%;
  box-sizing: border-box;
}

.menu-button {
  background-color: #FFFFFF;
  border-style: none solid none none;
  border-width: 1px;
  border-color: #E3DFDF;
  height: 40px;
  width: 256px;
  box-sizing: border-box;
}

.menu-button:hover {
  background-color: rgba(38,166,154,0.3);
}

.menu-button:focus {
  outline: none;
}

