.signed-tx {
    margin: 10px;
}

.signed-tx-input {
    background-color: #FFFFFF;
    margin: 10px;
    display: block;
    width: 50%;
    height: 100px;
    border: none;
}

.signed-tx-input:focus {
    outline-color: rgba(38,166,154,0.3);
}

.signed-tx-error {
    background-color: #FFFFFF;
    margin: 10px;
    display: block;
    width: 50%;
    height: 100px;
    border: solid red 2px;
}

.sign-tx-gray-box {
    background-color: white;
    overflow-wrap: break-word;
    padding: 20px;
    margin: 15px;
    border: solid 2px #26A69A;
}

.error-message {
    color: red;
    display: block;
    font-size: 14px;
    padding-bottom: 10px;
}

.import-button {
    background-color: #FFFFFF;
    border: 1px solid #D8DDE6;
    height: 32px;
    width: 118px;
    border-radius: 16px;
    color: #26A69A;
    font-family: Quicksand, serif;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 0;
    line-height: 15px;
    text-align: center;
    outline: none;
}

.field-title {
    font-weight: bold;
    padding-left: 15px;
}

.field-value {
    font-weight: normal;
    float: right;
    margin-right: 15px;
}

.tx-row {
    margin: 15px;
}

.tx-hash {
    padding: 15px;
    overflow-wrap: break-word;
    font-weight: normal;
}

.successful-tx {
    font-family: Roboto, serif;
    font-weight: bold;
    padding: 20px;
}

.disconnected-tooltip {
    font-weight: 500;
}

.number {
    font-weight: 400;
}
