.unlock-popup {
    position: absolute;
    background-color: #FFFFFF;
    width: 500px;
    height: 300px;
    border-radius: 4px;
    box-shadow: 0 2px 7px 2px rgba(0,0,0,0.23);
    margin: -50px 0 0 -100px;
    top: 30%;
    left: 50%;
    font-family: Roboto, serif;
    font-weight: bold;
    color: #444444;
    padding: 15px;
}

.password-field {
    font-family: Roboto, serif;
    font-size: 16px;
    font-weight: normal;
    padding: 15px 15px 0 15px;
}

.password-box {
    border-style: solid;
    border-color: #26A69A;
    border-width: 2px;
    height: 20px;
    width: 250px;
}

.password-box-warning {
    border-style: solid;
    border-color: red;
    border-width: 2px;
    height: 20px;
    width: 250px;
}

.password-box-warning:focus {
    outline: none;
}

.password-box:focus {
    outline: none;
}

.close-unlock-popup {
    background-color: #FFFFFF;
    border: none;
    float: right;
    height: 18px;
    width: 18px;
    text-align: center;
    margin-right: 20px;
}

.close-unlock-popup:focus {
    outline: none
}

.close-unlock-popup:hover {
    cursor: pointer;
}

.wrong-password {
    font-size: 12px;
    color: red;
    float: right;
    padding-right: 150px;
}