.confirm-online-popup {
    position: absolute;
    background-color: #FFFFFF;
    width: 540px;
    height: 330px;
    border-radius: 4px;
    box-shadow: 0 2px 7px 2px rgba(0,0,0,0.23);
    margin: -50px 0 0 -100px;
    top: 90%;
    left: 45%;
}

.popUp-content {
    position: absolute;
    color: #444444;
    font-family: Roboto, serif;
    width: 100%;
    height: 100%;
    margin-left: 15px;
}

.popup-header {
    font-size: 18px;
    font-weight: bold;
}

.from-box {
    background-color: rgba(38,166,154,0.15);
    font-size: 16px;
    font-weight: 400;
    padding-bottom: 10px;
    width: 40%;
    float: left;
    padding-left: 10px;
    padding-right: 10px;
}

.to-box {
    background-color: rgba(38,166,154,0.15);
    font-size: 16px;
    font-weight: 400;
    padding-bottom: 10px;
    width: 40%;
    float: right;
    margin-right: 30px;
    padding-left: 10px;
    padding-right: 10px;
    max-height: 200px;
    overflow: scroll;
}

.boxes {
    display: inline-block;
}

.amount-sent {
    font-size: 20px;
}

