.data-table {
    margin: 10px;
    border-spacing: 0px;
}

.data-header {
    border-bottom: solid black 1px;
}

.data-row {
    border-right: solid black 1px;
    vertical-align: top;
}

.output-row {
    padding-left: 10px;
    overflow-wrap: break-word;
    display: inline-block;
    width: 63%;
}

.box-drop {
    background-color: rgba(38,166,154,0);
    outline: none;
    border: none;
}