.balance-row{
    margin-bottom: 18px;
    margin-top: 10px;
}
.amount {
    font-family: Roboto, serif;
    color: rgba(0,0,0,0.85);
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    padding-left: 10px;
}

.icon {
    height: 51px;
    width: 51px;
    vertical-align: middle;
}

.action-buttons {
    float: right;
    margin-right: 20px;
}

.action-button-closed {
    background-color: #FFFFFF;
    border: none;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
    vertical-align: middle;
    text-align: center;
    margin-right: 10px;
}

.action-button-closed:focus {
    outline: none;
}

.action-button-closed:hover {
    color: #26A69A;
    font-weight: bold;
}

.action-button-opened {
    background-color: #FFFFFF;
    border-style: none none solid none;
    border-width: 3px;
    border-color: #26A69A;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: bold;
    line-height: 16px;
    color: #26A69A;
    vertical-align: middle;
    text-align: center;
    margin-right: 10px;
}

.action-button-opened:focus {
    outline: none;
}