.blue-square-button {
    height: 40px;
    min-width: 144px;
    background-color: #26A69A;
    border: none;
    color: #FFFFFF;
    font-family: Roboto, serif;
    font-size: 18px;
    font-weight: bold;
    line-height: 21px;
    float: right;
    margin-right: 20px;
    margin-bottom: 20px;
}

.blue-square-button:hover {
    border: solid #FFFFFF;
}

.blue-square-button:focus {
    outline: none
}

.disabled-square-button {
    height: 40px;
    width: 144px;
    background-color: #CCCCCC;
    border: none;
    color: #FFFFFF;
    font-family: Roboto, serif;
    font-size: 18px;
    font-weight: bold;
    line-height: 21px;
    float: right;
    margin-right: 20px;
    margin-bottom: 20px;
    outline: none;
}

.disabled-square-button:hover {
    cursor: not-allowed;
}


.round-blue-button {
    background-color: #26A69A;
    color: #FFFFFF;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: 600;
    outline: none;
    border: none;
    border-radius: 16px;
    width: 108px;
    height: 32px;
}

.text-button {
    font-family: Roboto, serif;
    font-size: 15px;
    background-color: transparent;
    border: none;
    outline: none;
    color: #26A69A;
}

.text-button:hover {
    text-decoration-line: underline;
}