.dropdown-button {
    font-family: "Roboto", serif;
    font-size: 16px;
    color: rgba(0,0,0,0.85);
    width: 200px;
    vertical-align: middle;
    padding: 5px 10px 2px 12px;
    outline: none;
    background-color: white;
    border: 2px solid #CCCCCC;
    margin-right: 15px;
}

.dropdown {
    margin: 0;
    width: 200px;
    border-radius: 4px;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2);
    position: absolute;
    z-index: 1;
    background-color: white;
}

.dropdown-title {
    font-family: "Helvetica Neue", serif;
    font-size: 16px;
    color: #444444;
    text-align: center;
    border-style: none none solid none;
    border-width: 2px;
    margin: 0;
    padding-bottom: 10px;
    padding-top: 10px;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
}

.dropdown-item {
    text-align: left;
    float: none;
    display: block;
    border: none;
    background-color: #FFFFFF;
    outline: none;
    height: 38px;
    width: 200px;
}

.dropdown-item:hover {
    background-color: #F2F2F2;
}