.card-outline {
    width: 300px;
    height: 200px;
    background-color: #FFFFFF;
    font-family: Roboto, serif;
    font-size: 18px;
    margin-top: 25px;
    overflow: auto;
    padding: 20px;
    margin-right: 20px;
}

.card-outline:hover {
    border-radius: 4px;
    box-shadow: 6px 6px 12px 6px rgba(0,0,0,0.2);
}

.wallet-header {
    margin-top: 5px;
    margin-bottom: 5px;
}

.current-button {
    border: none;
    outline: none;
    width: 108px;
    height: 32px;
    background-color: #FFFFFF;
    border-radius: 16px;
    color: #26a69a;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: 600;
}

.ellipse-button {
    border: none;
    outline: none;
    background-color: #26a69a;
}