.edit-info {
    font-family: Roboto, serif;
}

.edit-info:focus {
    outline: none;
}

.error-button {
    background-color: red;
    color: #FFFFFF;
    border: none;
    outline: none;
    width: fit-content;
    cursor: auto;
}

.currently-connected-button {
    background-color: green;
}

.status {
    list-style-type: none;
}

.status b{
    display: inline-block;
    width: 128px;
    position: relative;
    padding-right: 10px;
    font-weight: 500;
}

.status b::after {
    right: 10px;
}

.key-file-directory {
    border-style: solid;
    width: fit-content;
    padding: 11px 10px 10px 15px;
    display: inline-block;
    margin-left: 10px;
    background-color: transparent;
    border-width: 1px;
    white-space:nowrap;
}