.asset-popup {
    background-color: #FFFFFF;
    width: 500px;
    height: 230px;
    font-family: Roboto, serif;
    font-weight: bold;
    color: #444444;
    padding: 15px;
    border-style: solid;
    border-color: rgba(38,166,154,0.5);
    border-width: 2px;
    margin-top: 10px;
    margin-bottom: 20px;
}

.input-field {
    font-weight: normal;
    font-size: 16px;
    padding-left: 13px;
    padding-top: 15px;
    padding-bottom: 10px;
}

.input-box:focus {
    outline-color: #26A69A;
}

.input-warning-box {
    border-color: red;
    border-width: 2px;
    border-style: solid;
}

.input-warning-box:focus {
    outline-color: #26A69A;
    border-color: #26A69A;
}

.missing-fields {
    font-size: 12px;
    color: red;
    font-weight: 500;
}

.close-new-asset {
    background-color: #FFFFFF;
    border: none;
    float: right;
    height: 18px;
    width: 18px;
    text-align: center;
    margin-right: 20px;
}

.close-new-asset:hover{
    cursor: pointer;
}

.close-new-asset:focus {
    outline: none
}