.new-keyfile-popup {
    font-family: Roboto, serif;
    position: absolute;
    background-color: #FFFFFF;
    width: 540px;
    height: 350px;
    border-radius: 4px;
    box-shadow: 0 2px 7px 2px rgba(0,0,0,0.23);
    margin: -50px 0 0 -100px;
    top: 30%;
    left: 40%;
    padding: 15px 15px 0 15px;
}

.name-field {
    padding-bottom: 35px;
}

.input-style {
    border-style: solid;
    border-color: #26A69A;
    border-width: 2px;
    height: 30px;
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: Roboto, serif;
    font-size: 14px;
}

.input-style:focus {
    outline: none;
}

.input-warning-style {
    border-style: solid;
    border-color: red;
    border-width: 2px;
    height: 30px;
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 14px;
}

.input-warning-style:focus {
    outline: none;
}

.password {
    padding-right: 35px;
    float: left;
    width: 43%;
}