.top-bar {
    width: 100%;
    height: 50px;
}

.top-right-icons {
    padding-top: 5px;
    float: right;
    font-family: "Work Sans", serif;
    font-size: 16px;
    color: #444444;
    display: flex;
}

.side-menu-button {
    background-color: #FFFFFF;
    border: none;
}

.side-menu-button:hover {
    cursor: pointer;
}

.side-menu-button:focus {
    outline: none;
}

.username{
    font-weight: bold;
    padding-left: 15px;
    padding-top: 5px;
}