.popup {
    overflow: auto;
    font-family: Roboto, serif;
    position: fixed;
    background-color: #FFFFFF;
    width: 540px;
    height: 350px;
    border-radius: 4px;
    box-shadow: 0 2px 7px 2px rgba(0,0,0,0.23);
    margin: -50px 0 0 -100px;
    top: 30%;
    left: 40%;
    padding: 15px 15px 0 15px;
    z-index: 1;
}