.outer-gray {
    padding-top: 20px;
    padding-left: 20px;
    width: 100vw;
    max-width: 100%;
    min-height: 100vh;
    background-color: rgba(216,216,216,0.25);
    overflow: auto;
}

.inner-box{
    background-color: #FFFFFF;
    padding: 44px 0 10px 44px;
    margin: 44px 44px 44px 0;
    font-family: 'Roboto', serif;
    color: rgba(0,0,0,0.85);
    overflow: visible;
}

.table-title{
    font-size: 20px;
    font-weight: 500;
    padding-bottom: 30px;
}

.refresh-symbol {
    width: 23px;
    height: 23px;
    color: rgba(0,0,0,0.85);
    padding-left: 5px;
}

.refresh-button {
    background-color: #FFFFFF;
    border: none;
    outline: none;
    font-family: Roboto, serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
    vertical-align: middle;
    text-align: center;
    margin-right: 10px;
}