.other-assets{
    text-align: center;
    font-size: 14px;
    border-collapse: collapse;
    width: 90%;
    min-width: 800px;
    margin-bottom: 10px;
}

.table-header{

    border-style: none none solid none;
    border-width: 1px;
    text-align: center;
    padding: 8px;
    font-weight: bold;
}

.table-header-actions{
    border-style: none none solid none;
    border-width: 1px;
    text-align: right;
    padding: 8px;
    font-weight: bold;
}

.asset-cell {
    padding: 8px;
    text-align: center;
    font-size: 12px;
}


.asset-table-action-buttons {
    float: right;
    margin-right: 5px;
}

.asset-table-action-button{
    background-color: #FFFFFF;
    border: none;
}

.asset-table-action-button:focus {
    outline: none;
}
